<template>
<!-- 建议给每一个组件的根标签设置一个单独的类名，以此做区分 -->
<div class="login-container">
<!-- 导航栏 -->
    <van-nav-bar title="登录" class="page-nav">
    <template #left>
      <!-- $router.back()可以实现返回的操作。能够让路由退回到前一步 -->
    <van-icon name="cross" @click="$router.back()" />
  </template>
    </van-nav-bar>
<!-- 登录表单 -->
<!-- ref="loginForm"配合$refs用来获取手机号 -->
<van-form @submit="onSubmit" ref="loginForm">
  <van-field
    v-model="user.mobile"
    name="mobile"
    label="手机号"
    placeholder="手机号"
    :rules="userFormRules.mobile"
  >
  <template #left-icon>
<i class="toutiao toutiao-shouye"></i>
  </template>
  </van-field>
  <van-field
    v-model="user.code"
    type="password"
    name="code"
    label="验证码"
    placeholder="验证码"
    :rules="userFormRules.code"
  >
   <template #left-icon>
<i class="toutiao toutiao-yanzhengma"></i>
  </template>
    <template #button>
      <!-- van-count-down   vant中的倒计时组件 -->
      <van-count-down
  :time="1000 * 60"
  format="ss 秒"
  v-if="isCountDownShow"
  @finish="isCountDownShow=false"
/>
<!-- type="default"设置发送验证码颜色，default为默认颜色 -->
<!-- 设置native-type="button"属性，防止触发表单提交事件 -->
<!-- 创建点击事件@click="onSendSms" -->
    <van-button size="small" type="default" native-type="button" @click="onSendSms" v-else>发送验证码</van-button>
  </template>
  <!-- i slot="left-icon"老版本的写法，只是在慢慢废弃。因此不建议使用 -->
  <!-- 优点：如果只是作为一个标签，并不需要用到作用域插槽，可以减少代码 -->
  <!--<i slot="left-icon" class="toutiao toutiao-yanzhengma"></i> -->
</van-field>
  <div class="login-btn-wrap">
    <van-button  block type="info" class="login-btn" native-type="submit">登录</van-button>
  </div>
</van-form>
</div>
</template>
<!--
- 注册点击登录的事件
- 获取表单数据（根据接口要求使用 v-model 绑定）
- 表单验证
- 发请求提交
- 根据请求结果做下一步处理
-->
<script>
import { loginAPI, getSmsCodeAPI } from '../../api/index.js'
export default {
  name: 'LoginIndex',
  data () {
    return {
      // 为什么要传对象的原因是后端要求传递mobile和code去后端
      user: {
        mobile: '',
        code: ''
      },
      // 表单验证规则
      // 1.创建表单验证规则对象，对象中的每一个属性代表的是页面上的表单控件
      // 2.分析每一个控件需要的规则
      // 3.根据vant文档中rules的相关规则属性来进行规则的书写
      userFormRules: {
        // 手机号验证规则
        // 1.必填
        // 2.手机号的基本结构  11位。第一位是1，第二位是（3479），后面为任意  --正则表达式
        mobile: [
          { required: true, message: '手机号不能为空' },
          {
            pattern: /^1[3|5|7|8]\d{9}$/,
            message: '手机号格式错误'
          }
        ],
        // 验证码验证规则
        // 1.必填
        // 2.验证码必须时六位有效数字  --正则表达式
        code: [{ required: true, message: '验证码不能为空' },
          {
            pattern: /^\d{6}$/,
            message: '验证码格式错误'
          }]
      },
      //
      isCountDownShow: false
    }
  },
  methods: {
    async onSubmit () {
      // 发起登录时，需要弹出加载中的遮罩，防止用户多次操作
      // 在Js中使用带大写的vant组件库的东西时会报错，解决方法有两种
      // 第一种：import { Toast } from 'vant';
      // 第二种：在大写单词前面加this.$并将大写转为小写
      // 将toast加载的提示设置为持续提示状态，在请求接口后响应数据后主动的关闭
      // 在请求成功后都需要通过toast.clear（）关闭遮罩层
      const toast = this.$toast.loading({
        duration: 0, // 持续展示 toast
        message: '加载中...',
        forbidClick: true
      })
      console.log('submit', this.user)
      try {
        const res = await loginAPI({
          mobile: this.user.mobile,
          code: this.user.code
        })
        toast.clear() // 关闭遮罩层
        // 当提示登录成功或失败时，会自动把其他的toast提示清除掉。即不写toast.clear()也可以
        this.$toast.success('登录成功')
        console.log('登录成功', res)
        // 登录成功时，立即关闭倒计时---并开启跳转或者直接跳转
        this.isCountDownShow = false
        // 登录成功后需要做的业务逻辑
        // 需要把返回的两个token放在vueX状态管理器中
        // 通过 this.$store.commit(mutations中定义的方法名，需要传递的参数)方法去调用mutations中定义的方法
        this.$store.commit('setUser', res.data.data)
        // 通过$router.back进行页面路由的退回
        this.$router.back()
      } catch (error) {
        // response和status都是通过直接在catch(error)中直接打印console.log(error)得到的
        if (error.response.status === 400) {
          // 此处我省略了toast.clear()
          this.$toast.fail('手机号或者验证码错误')
          console.log('手机号或者验证码错误', error)
        } else {
          // 此处我省略了toast.clear()
          this.$toast.fail('系统错误，请稍后重试')
          console.log('系统错误，请稍后重试', error)
        }
      }
    },
    // 发送验证码
    async onSendSms () {
    // 1.验证手机号格式规范
    // 此处的mobile来自于表单控件的name属性的属性值
    // console.log(this.$refs.loginForm.validata('mobile'))
    // 通过打印可以看到值有promise对象。看到这个就想到用async和await去接收处理
      try {
        await this.$refs.loginForm.validate('mobile')
        console.log('验证成功')

        // 如果手机号通过验证，则发送验证码，并显示再次发送验证码的倒计时
        this.isCountDownShow = true
      } catch (error) {
        console.log('验证失败', error)
      }
      // 请求发送验证接口
      try {
        // 短信功能不用关心接口调用成功的返回参数，只需要关心是否调用成功
        await getSmsCodeAPI(this.user.mobile)
        this.$toast('验证码发送成功')
      } catch (error) {
        // 发送接口失败时，立即关闭倒计时
        this.isCountDownShow = false
        // 通过保存的code提示来进行区别展示
        if (error.response.status === 429) {
          this.$toast.fail('发送过于频繁，请稍后再试')
        } else {
          this.$toast.fail('发送失败，请重试')
        }
      }
    }
  }
}
</script>

<style scoped lang="less">
/* 只要是项目就会存在统一的色调主题，
这个主题色调就可能体现在顶部和底部导
航，选中的选项中。因此这些主题颜色的css样式，
就应该写在index.less中 */
.login-container{
/* 设置图标的大小 */
.toutiao{
font-size:32px;
}
}
.login-container {
  .login-btn-wrap {
    padding: 53px 33px;
    .login-btn {
      background-color: #6db4fb;
      border: none;
    }
  }
  .page-nav{
    //设置退出按钮的样式    van-icon自带的
    .van-icon{
      color:#fff
    }
  }
}
</style>
